import React from 'react';
import GalleryItem from './GalleryItem';

function Gallery() {
  return (
    <div className="gallery-container">
      <GalleryItem
        title="MyList"
        description="Organize all your recipes in one place"
        backgroundImage="/gallery1.png"  
        externalLink="https://example.com/recipe-manager" 
      />
      <GalleryItem
        title="Favorite"
        description="Access your top recipes from just a click"
        backgroundImage="/gallery2.png"
        externalLink="https://example.com/meal-planner" 
      />
      <GalleryItem
        title="Browse"
        description="Settle your curiosity with more recipes"
        backgroundImage="/gallery3.png"
        externalLink="https://example.com/shopping-list"
      />
    </div>
  );
}

export default Gallery;
