import React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import { Box, Menu, MenuItem, Container, Link, TextField, InputAdornment } from '@mui/material';

function Navbar() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <AppBar position="static" style={{ backgroundColor: '#ffffff', boxShadow: 'none' }}>
        <Container maxWidth="xl">
          <Toolbar disableGutters>
            <Box sx={{ display: 'flex', mr: 2 }}>
              <IconButton onClick={handleMenuClick} color="black">
                <MenuIcon />
              </IconButton>
              <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleMenuClose}>
                <MenuItem onClick={handleMenuClose}>
                  <Link href="/" color="inherit" underline="none" sx={{ fontWeight: 'bold', fontFamily: 'Arial, sans-serif' }}>
                    Home
                  </Link>
                </MenuItem>
                <MenuItem onClick={handleMenuClose}>
                  <Link href="./recipeForm.jsx" color="inherit" underline="none" sx={{ fontWeight: 'bold', fontFamily: 'Arial, sans-serif' }}>
                    AddRecipe
                  </Link>
                </MenuItem>
                <MenuItem onClick={handleMenuClose}>
                  <Link href="/shoppingList" color="inherit" underline="none" sx={{ fontWeight: 'bold', fontFamily: 'Arial, sans-serif' }}>
                    ShoppingList
                  </Link>
                </MenuItem>
              </Menu>
            </Box>

            <Typography
              variant="h6"
              noWrap
              component="a"
              href="/"
              sx={{
                display: { xs: 'none', md: 'flex' },
                fontFamily: 'monospace',
                fontWeight: 700,
                letterSpacing: '.3rem',
                color: 'inherit',
                textDecoration: 'none',
                flexGrow: 1,
              }}
            >
              <img
                src="https://cdn.prod.website-files.com/63bb7fe09d70bb7dc8e86719/63bcbeb7701f9702f397b2ab_cookbook%20blue%20cropped.png"
                alt="CookBook Manager Logo"
                style={{ width: '200px', height: 'auto' }}
              />
            </Typography>

            <TextField
              variant="outlined"
              size="small"
              placeholder="Search..."
              sx={{
                backgroundColor: '#f4f2f0',
                borderRadius: '4px',
                '& .MuiOutlinedInput-root': {
                  '& fieldset': {
                    borderColor: '#ccc',
                  },
                  '&:hover fieldset': {
                    borderColor: '#aaa',
                  },
                  '&.Mui-focused fieldset': {
                    borderColor: '#666',
                  },
                },
              }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
            />
          </Toolbar>
        </Container>
      </AppBar>

      <div className="wave-navbar"></div>
    </>
  );
}

export default Navbar;
